const transformCreateNote = (manager) => {
  manager.addField('Priority');
  manager.transformApolloConfig('props', ({ mutate }) => (prevProps) => {
    const onAdd = (content, priority) => {
      mutate({
        variables: {
          Input: {
            Content: content,
            Priority: priority
          }
        }
      });
    };

    return {
      ...prevProps,
      onAdd,
    };
  })
};

export default transformCreateNote;


